import React from "react";
import { HotelPostGridLoader } from "components/ContentLoader/ContentLoader";
import BaseIndexPage from "components/HOC/BaseIndexPage";
import SectionGrid from "components/global-components/Components/SectionGrid/SectionGrid.cra";
import Navbar from "components/global-components/navbar";
import useWindowSize from "lib/helpers/useWindowSize";
import size from "lodash/size";
import { Fragment, useContext, useEffect, useState } from "react";
import { connect } from "react-redux";
import styled from "styled-components";
import { get, getList, update, saveSearch } from "../../state/Listing/Epic";
import { create, remove } from "../../state/Favourite/Epic";
import { selectList, selectSearch } from "../../state/Listing/Selectors";
import FilterForm from "./Component/FilterForm";

import { AuthContext } from "Boot/auth/AuthProvider";
import { PROPERTY } from "components/Routes/Constants";
import isNil from "lodash/isNil";
import ListingMap from "./Component/ListingMap";
import { PostsWrapper } from "./Listing.style";
import { PlaceAutocomplete } from "components/global-components/Components/Form/PlaceAutocomplete";
import { LoadScript } from "@react-google-maps/api";
import { isEmpty, isNull } from "lodash";
import { initialValues } from "./Component/InitialValues.json";
import { useFormik } from "formik";

const Wrapper = styled.div`
  padding-top: 80px;
`;

const RemoveBorderButton = styled.button`
  position: absolute;
  top: 10px;
  left: 184px;
  padding: 8px;
  border: 0;
  background: white;
  color: rgb(86, 86, 86);
  font-size: 13px;
  font-weight: 600;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 4px -1px;
  border-radius: 2px;
  border: 2px solid #2e6ca0;
  color: #2e6ca0;
`;

const getZoom = (address) => {
  if (isNil(address)) {
    return 3;
  } else {
    if (!isNil(address?.state)) {
      return 6;
    } else {
      return 10;
    }
  }
};

const Listing = ({
  getList,
  onEditItem,
  list,
  isLoading,
  history,
  search,
  onFavourite,
  update,
  onSubmitItem,
  createFavourite,
  removeFavourite,
  handleResponse,
  setSearch,
  ...rest
}) => {
  const {
    location: searchedLocation,
    bounds: searchedBounds,
    address: searchedAddress,
  } = search;
  const [showMap, setShowMap] = useState(true);
  const [location, setLocation] = useState(
    isNil(searchedLocation)
      ? { lat: 39.828175, lng: -98.5795 }
      : searchedLocation
  );
  const [bounds, setBounds] = useState(searchedBounds);
  const [address, setAddress] = useState(searchedAddress);
  const [zoom, setZoom] = useState(3);
  const [limit, setLimit] = useState(5);
  const [resetFlag, setResetFlag] = useState(false);
  const { user } = useContext(AuthContext);

  let userId = null;
  if (!isNull(user)) {
    userId = user._id;
  }

  useEffect(() => {
    rest.getIndex({ ...search, userId });
  }, []);

  useEffect(() => {
    if(!isNil(searchedAddress)){
      setZoom(getZoom(searchedAddress));
    }
  }, [searchedAddress]);

  const { width } = useWindowSize();
  let columnWidth = [1 / 2, 1 / 2, 1 / 3, 1 / 4, 1 / 5, 1 / 5, 1 / 6];

  if (showMap) {
    columnWidth = [1 / 2, 1 / 2, 1 / 2, 1 / 2, 1 / 2];
  }

  const handleMapToggle = () => {
    setShowMap((showMap) => !showMap);
  };

  const handleOnClick = ({ created_by, display_id }) => {
    //onEdit({ _id: display_id }, ({ data }) => {

    if (!isNil(user) && user._id === created_by) {
      history.push(`${PROPERTY}/${display_id}#step1`);
    } else {
      history.push(`/listing/${display_id}`);
    }

    // });
  };

  const handleFavourite = async (params) => {
    await createFavourite(params).then((response) => {
      handleResponse({ response });
      getList({ userId });
    });
  };

  const handleRemoveFavourite = async (display_id) => {
    console.log("display_id", display_id);
    removeFavourite(display_id)
      .then((response) => {
        getList({ userId });
      })
      .catch((err) => {});
  };

  const handleReset = (val) => {
    if(isEmpty(val)){
      setZoom(3);
    }
    setBounds();
    setAddress();
    setSearch({});
    setResetFlag(true);
    rest.getIndex({ userId });
    // window.location.reload();
  };

  return (
    <Fragment>
      <Navbar noBanner={true} />
      <FilterForm
        onShowMap={handleMapToggle}
        search={search}
        width={width}
        setResetFlag={setResetFlag}
        resetFlag={resetFlag}
        isShowMap={showMap}
        onReset={handleReset}
        onSubmit={(values) => {
          
          if(!isNil(values.location)){
            setLocation(values.location);
            const { state, ...restAddress } = values.address;
            setSearch(values.address);
            setZoom(getZoom(values.address));
            setBounds(values.bounds);
            setAddress(restAddress);
          }
          rest.getIndex({ ...values, userId });
        }}
      />
      <Wrapper>
        <PostsWrapper className={width > 767 && showMap ? "col-12" : "col-24"}>
          <SectionGrid
            link={"/singleproperty"}
            columnWidth={columnWidth}
            data={list}
            userId={userId}
            onEdit={onEditItem}
            onFavourite={handleFavourite}
            onRemoveFavourite={handleRemoveFavourite}
            totalItem={size(list)}
            loading={isLoading}
            limit={limit}
            handleLoadMore={() => setLimit(limit + 2)}
            placeholder={<HotelPostGridLoader />}
          />
        </PostsWrapper>
        {width > 767 && showMap && (
          <ListingMap
            handleOnClick={handleOnClick}
            data={list}
            bounds={bounds}
            location={location}
            zoom={zoom}
            address={address}
          >
            {address && (
              <RemoveBorderButton onClick={handleReset}>
                Remove Boundary
              </RemoveBorderButton>
            )}
          </ListingMap>
        )}
      </Wrapper>
    </Fragment>
  );
};

const mapStateToProps = (state) => {
  return {
    noBanner: true,
    isHideFooter: true,
    noHeader: true,
    list: selectList(state),
    search: selectSearch(state),
  };
};

const mapDispatchToProps = (dispatch) => ({
  get: (params) => dispatch(get(params)),
  update: (id, params) => dispatch(update(id, params)),
  createFavourite: (params) => dispatch(create(params)),
  getList: (params) => dispatch(getList(params)),
  removeFavourite: (params) => dispatch(remove(params)),
  saveSearch: (params) => dispatch(saveSearch(params)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseIndexPage(Listing));
