// src/components/GoogleMap.js
import { GoogleMap, InfoWindow, Marker, Polygon } from "@react-google-maps/api";
import React, { useRef, useState, useEffect } from "react";
import HotelInfoWindow from "./MapInfoWindow";
import { first, flatMap, head, isNull } from "lodash";

const containerStyle = {
  width: "100%",
  height: "400px",
};

const findFirstNotNullOrUndefinedAttribute = (obj) => {
  const [attributeName, attributeValue] =
    Object.entries(obj).find(
      ([_, value]) => value !== null && value !== undefined
    ) || [];

  
  return attributeName !== undefined ? { attributeName, attributeValue } : null;
};

const GoogleMapWithCityPolygon = ({ address }) => {
  const [map, setMap] = useState(null);
  const [cityData, setCityData] = useState(null);

  useEffect(() => {
    const fetchCityData = async () => {
      try {
        const {state:stateRemoved = "", ...restAddress} = address;
        const { attributeValue } = findFirstNotNullOrUndefinedAttribute(
          restAddress
        );
        
        const response = await fetch(
          `https://nominatim.openstreetmap.org/search.php?q=${encodeURIComponent(
            attributeValue
          )}&polygon_geojson=1&format=jsonv2`
        );
        

        if (!response.ok) {
          throw new Error(
            `Failed to fetch city data: ${response.status} - ${response.statusText}`
          );
        }

        const data = await response.json();

        if (data && data[0] && data[0].geojson) {
          setCityData(data[0].geojson);
        } else {
          throw new Error("City data not found in the response");
        }
      } catch (error) {
        console.error(error.message);
      }
    };
    

    fetchCityData();
  }, [address]);

  const handleLoad = (map) => {
    setMap(map);
  };

  return (
    !isNull(cityData) && (
      <>
        {cityData.coordinates.map((item) => {
          
          let polygon = item;

          if(cityData.type === "MultiPolygon") {
            polygon = flatMap(item);
          }
          return (
            <>
              <Polygon
                paths={polygon.map(([lng, lat]) => ({
                  lat,
                  lng,
                }))}
                options={{
                  fillColor: "#008000", // Fill color for the city polygon (green in this example)
                  strokeColor: "#008000", // Border color for the city polygon
                  strokeOpacity: 0.8,
                  strokeWeight: 2,
                }}
              />
            </>
          );
        })}
      </>
    )
  );
};

