export const GoogleMapsComponent = ({
  markers,
  location,
  zoom,
  bounds,
  address,
  children
}) => {
  const [selectedMarker, setSelectedMarker] = useState(null);
  const [forceUpdate, setForceUpdate] = useState(false);
  const infoWindowRef = useRef(null);
  const [path, setPath] = useState({
    Texas: [
      { lat: 36.5, lng: -103 },
      { lat: 36.5, lng: -94 },
      { lat: 32, lng: -94 },
      { lat: 32, lng: -106 },
      { lat: 36.5, lng: -106 },
    ],
  });


  const onMarkerClick = (marker) => {
    setSelectedMarker(marker);
  };

  return (
    <GoogleMap mapContainerStyle={containerStyle} center={location} zoom={zoom}>
      {children}
      {markers.map((marker, index) => {
        const { id, location } = marker;
        return (
          <Marker
            onClick={() => onMarkerClick(marker)}
            key={id}
            position={{ lat: location.lat, lng: location.lng }}
            title={"marker.title"}
          />
        );
      })}

      {selectedMarker && (
        <InfoWindow
          position={{
            lat: selectedMarker.location.lat,
            lng: selectedMarker.location.lng,
          }}
          ref={infoWindowRef}
          onCloseClick={() => setSelectedMarker(null)}
        >
          {/* <HotelMapMarkerSingle {...selectedMarker}/> */}
          <div style={{ maxWidth: "200px", minHeight: "200px" }}>
            <HotelInfoWindow
              {...selectedMarker}
              onCloseClick={() => {
                setForceUpdate((prevState) => !prevState);
              }}
            />
          </div>
        </InfoWindow>
      )}
      {address && <GoogleMapWithCityPolygon address={address} />}
    </GoogleMap>
  );
};
